




























import { Component, Vue, Watch } from 'vue-property-decorator'
import parsePhoneNumber from 'libphonenumber-js/max'
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
import UIkit from 'uikit/dist/js/uikit.min'

@Component({ name: 'SearchPage' })
export default class Search extends Vue {
  private searchValue = ''
  private hasError = false

  @Watch('searchValue')
  private searchValueChange () {
    this.hasError = false
    UIkit.notification.closeAll()
  }

  private searchTypes = [
    {
      searchType: 'email',
      normalize: (text: string): string => {
        return text
      },
      validation: (text: string): boolean => {
        return /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          .test(text)
      }
    },
    {
      searchType: 'name',
      normalize: (text: string): string => {
        return text.replace(' ', '_')
      },
      validation: (text: string): boolean => {
        const indexOfSpace = text.indexOf(' ')
        return indexOfSpace > -1 && text.length > 3 && !/[^a-zA-Z0-9а-яА-Я]/.test(text.replace(' ', ''))
      }
    },
    {
      searchType: 'ip',
      normalize: (text: string): string => {
        return text
      },
      validation: (text: string): boolean => {
        return (/^([0-9]{1,3})\.([0-9]{1,3})\.([0-9]{1,3})\.([0-9]{1,3})$/).test(text)
      }
    },
    {
      searchType: 'phone',
      normalize: (text: string): string => {
        return parsePhoneNumber(text, 'RU')!.number.toString()
      },
      validation: (text: string): boolean => {
        return !!parsePhoneNumber(text, 'RU')
      }
    },
    {
      searchType: 'nickname',
      normalize: (text: string): string => {
        return text[0] === '@' ? text : `@${text}`
      },
      validation: (text: string): boolean => {
        return text.length > 2 && !/[^@a-zA-Z0-9а-яА-я]/.test(text)
      }
    }
  ]

  private submitHandle (): void {
    let typeOverlap = false

    for (let i = 0; i < this.searchTypes.length; i++) {
      if (this.searchTypes[i].validation(this.searchValue)) {
        typeOverlap = true
        this.$router.push(`${this.searchTypes[i].searchType}/${this.searchTypes[i].normalize(this.searchValue)}`)
        break
      }
    }
    // If type hasn't overlap
    if (!typeOverlap) {
      this.hasError = true
      UIkit.notification({
        message: 'Parsing Error',
        status: 'danger',
        pos: 'top-center',
        timeout: 5000
      })
    }
  }
}
